/**
 * 1.0 - Body
 */
body {
  color: #3F444A;
  font-family: 'Aleck', Arial, sans-serif;
  font-size: 15px;
  font-weight: 500;
  line-height: 1.525;
  margin: 20px 40px;
  max-width: 600px;
}

body.post-type-page {
  max-width: 840px;
}

/**
 * 2.0 - Typography
 */
h1, h2, h3, h4, h5, h6 {
  margin-top: 1.4em;
}

h1 {
  font-size: 22px;
  line-height: 1.45;
}

h2 {
  font-size: 22px;
  line-height: 1.4;
  font-weight: 600;
  color: #0568ae;
}

h3 {
  font-size: 19px;
  font-weight: 600;
  line-height: 1.5;
  color: #0568ae;
  padding-right: 60px;
}

h4 {
  font-size: 18px;
  font-weight: 600;
  line-height: 1.5;
  color: #ea7400;
  padding-right: 60px;
}

h5 {
  font-size: 15px;
  font-weight: 600;
  line-height: 1.6;
}

h6 {
  font-size: 14px;
  font-weight: 700;
}

/*
|------------------------------------------------
| LISTS
|------------------------------------------------
*/
ul,
ol {
  padding: 0;
  margin: 0 0 1em 1.655em;
}

ul ul,
ul ol,
ol ol,
ol ul {
  margin-bottom: 0;
}

ul {
  list-style: disc;
}

ol {
  list-style: decimal;
}

li {
  line-height: 1.7;
}

ul.unstyled,
ol.unstyled {
  margin-left: 0;
  list-style: none;
}

ul.inline,
ol.inline {
  margin-left: 0;
  list-style: none;
}

ul.inline > li,
ol.inline > li {
  display: inline-block;
  padding-left: 1em;
  padding-right: 1em;
}

ol ol {
  list-style: lower-alpha;
}

ol ol ol {
  list-style: lower-roman;
}

dl {
  margin-bottom: 1em;
}

dt, dd {
  line-height: 1.7;
}

dt {
  font-weight: 700;
}

dd {
  margin-left: 0;
}

hr {
  height: 0;
  margin: 1em 0;
  border: 0;
  border-top: 2px solid #b5b8bb;
}

p {
  margin: 0 0 20px;
}

b,
strong {
  font-weight: 700;
}

dfn,
cite,
em,
i {
  font-style: italic;
}

blockquote {
  border-left: 4px solid #1a1a1a;
  color: #686868;
  font-size: 19px;
  font-style: italic;
  line-height: 1.4736842105;
  margin-bottom: 28px;
  padding: 0 0 0 24px;
}

blockquote:not(.alignleft):not(.alignright) {
  margin-left: -28px;
}

blockquote blockquote:not(.alignleft):not(.alignright) {
  margin-left: 0;
}

blockquote:before,
blockquote:after {
  content: "";
  display: table;
}

blockquote:after {
  clear: both;
}

blockquote > :last-child {
  margin-bottom: 0;
}

blockquote cite,
blockquote small {
  color: #1a1a1a;
  font-size: 16px;
  line-height: 1.75;
}

blockquote em,
blockquote i,
blockquote cite {
  font-style: normal;
}

blockquote strong,
blockquote b {
  font-weight: 600;
}

blockquote.alignleft,
blockquote.alignright {
  border: 0 solid #1a1a1a;
  border-top-width: 4px;
  padding: 18px 0 0;
  width: -webkit-calc(50% - 14px);
  width: calc(50% - 14px);
}

address {
  font-style: italic;
  margin: 0 0 28px;
}

code,
kbd,
tt,
var,
samp,
pre {
  font-family: Inconsolata, monospace;
}

pre {
  border: 1px solid #d1d1d1;
  font-size: 16px;
  line-height: 1.3125;
  margin: 0 0 28px;
  max-width: 100%;
  overflow: auto;
  padding: 14px;
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word;
}

code {
  background-color: #d1d1d1;
  padding: 2px 4px;
}

abbr[title] {
  border-bottom: 1px dotted #d1d1d1;
  cursor: help;
}

mark,
ins {
  background: #007acc;
  color: #fff;
  padding: 2px 4px;
  text-decoration: none;
}

sup,
sub {
  font-size: 75%;
  height: 0;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  top: -6px;
}

sup {
  bottom: -3px;
}

small {
  font-size: 80%;
}

big {
  font-size: 125%;
}

/**
 * 3.0 - Elements
 */
dl {
  margin: 0 0 28px;
}

dt {
  font-weight: bold;
}

dd {
  margin: 0 0 28px;
}

table,
th,
td,
.mce-item-table,
.mce-item-table th,
.mce-item-table td {
  border: 1px solid #e7e7e7;
}

table a {
  color: #0473ab;
}

table,
.mce-item-table {
  border-collapse: separate;
  border-spacing: 0;
  border-width: 1px 0 0 1px;
  margin: 0 0 28px;
  width: 100%;
}

table th,
.mce-item-table th,
table caption {
  border-width: 0 1px 1px 0;
  font-size: 16px;
  font-weight: 700;
  padding: 7px;
  text-align: left;
  vertical-align: baseline;
}

table td,
.mce-item-table td {
  border-width: 0 1px 1px 0;
  font-size: 16px;
  padding: 7px;
  vertical-align: baseline;
}

img {
  border: 0;
  height: auto;
  max-width: 100%;
  vertical-align: middle;
}

a img {
  display: block;
}

figure {
  margin: 0;
}

del {
  opacity: 0.8;
}

a {
  box-shadow: 0 1px 0 0 currentColor;
  color: #007acc;
  text-decoration: none;
}

fieldset {
  border: 1px solid #d1d1d1;
  margin: 0 0 28px;
  padding: 14px;
}

/**
 * 4.0 - Alignment
 */
.alignleft {
  float: left;
  margin: 6px 28px 28px 0;
}

.alignright {
  float: right;
  margin: 6px 0 28px 28px;
}

.aligncenter {
  clear: both;
  display: block;
  margin: 0 auto 28px;
}

/**
 * 5.0 - Caption
 */
.wp-caption {
  background: transparent;
  border: none;
  margin-bottom: 28px;
  max-width: 100%;
  padding: 0;
  text-align: inherit;
}

.wp-caption-text,
.wp-caption-dd {
  color: #686868;
  font-size: 13px;
  font-style: italic;
  line-height: 1.6153846154;
  padding-top: 7px;
}

/**
 * 6.0 - Galleries
 */
.mce-content-body .wpview-wrap {
  margin-bottom: 28px;
}

.gallery {
  margin: 0 -1.1666667%;
  padding: 0;
}

.gallery .gallery-item {
  display: inline-block;
  max-width: 33.33%;
  padding: 0 1.1400652% 2.2801304%;
  text-align: center;
  vertical-align: top;
  width: 100%;
}

.gallery-columns-1 .gallery-item {
  max-width: 100%;
}

.gallery-columns-2 .gallery-item {
  max-width: 50%;
}

.gallery-columns-4 .gallery-item {
  max-width: 25%;
}

.gallery-columns-5 .gallery-item {
  max-width: 20%;
}

.gallery-columns-6 .gallery-item {
  max-width: 16.66%;
}

.gallery-columns-7 .gallery-item {
  max-width: 14.28%;
}

.gallery-columns-8 .gallery-item {
  max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
  max-width: 11.11%;
}

.gallery .gallery-caption {
  font-size: 13px;
  margin: 0;
}

.gallery-columns-6 .gallery-caption,
.gallery-columns-7 .gallery-caption,
.gallery-columns-8 .gallery-caption,
.gallery-columns-9 .gallery-caption {
  display: none;
}

/**
 * 7.0 - Audio / Video
 */
.wp-audio-shortcode a,
.wp-playlist a {
  box-shadow: none;
}

.mce-content-body .wp-audio-playlist {
  margin: 0;
  padding-bottom: 0;
}

.mce-content-body .wp-playlist-tracks {
  margin-top: 0;
}

.mce-content-body .wp-playlist-item {
  padding: 10px 0;
}

.mce-content-body .wp-playlist-item-length {
  top: 10px;
}
